<template>
  <div>
     
    <v-container align="center">
         <h3>Unpaid Hours</h3>
      <div align="center">
        <tr v-for="(hour, index) in hours" :key="index">
          <td>
            <v-chip @click='click(hour)'>
              <v-avatar class="teal"></v-avatar>
              <strong>{{ hour.Name }}</strong>&nbsp;
            </v-chip>
          </td>
          <td>
            <v-chip>
              <v-avatar class="teal"></v-avatar>
              <strong>{{ hour.Hours }}</strong>&nbsp;
            </v-chip>
          </td>
        </tr>
      </div>
    </v-container>
    <v-dialog ref="dialog" v-model="view" max-width="1000">
      <v-card>
        <div>
          <Review ref='form'/>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Axios from 'axios';
import Review from './Review.vue'
export default {

    components : {
      Review
    },

    data() {
        return {
            hours: [],
            view: false
        }
    },

    methods : {
        importData(){
            const path = 'http://54.206.58.233:5000/hours/employees/get/unpaid'
            Axios.get(path)
            .then((res)=>{
                this.hours = res.data
            })
        },
        click(hour) {
            this.view= true
            this.$refs.form.updateData(0,hour.ID,0,1)
        }
    },
    created() {
        Axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
        this.importData()
    }


}
</script>

