<template>
  <div>
    <v-container>
    </v-container>
    <b-container>
      <table class="table table-hover">
            <thead>
              <tr>
                <th scope="col" width=200>Job Name</th>
                <th scope="col"  width=200>Hours</th>
                <th scope="col"  width=200>Date</th>
                <th scope="col"  width=100>Payed</th>

              </tr>
            </thead>
            <tbody>
              <tr v-for="(hour, index) in hours" :key="index" >
                <td>{{ hour.job }}</td>
                <td>{{hour.Hours}}</td>
                <td>{{hour.Date}}</td>
                <td>
                  <v-checkbox type="checkbox" v-model="hour.approved"></v-checkbox>
                </td>
              </tr>
            </tbody>
          </table>
    </b-container>
    <b-container>
      <div align="right">
        <button type="button" class="btn btn-success btn-sm" @click="logData()">Submit</button>
      </div>
    </b-container>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  data() {
    return {
      tokenStr: '',
      menu: false,
      hours: [],
      Id: '',
      JobID: 0,
      flag: 0,
    };
  },
  methods: {

	updateData(date, Id, JobID, flag) {

	      this.date = date;

	      this.Id = Id;

	      this.JobID = JobID;

	      this.flag = flag;

	      this.importData();

	    },


    importData() {
      const path = 'http://54.206.58.233:5000/hours/get/approved';
      const payload = {
        Date: this.date,
        ID: this.Id,
        JobID: this.JobID,
        flag: this.flag,
      };
      axios.post(path, payload)
        .then((res) => {
          console.log(res.data);
          this.hours = res.data;
        })
        .catch((error) => {
          // eslint-disable-next-line
              console.error(error);
        });
    },
    logData() {
      const path = 'http://54.206.58.233:5000/hours/approve';
      const payload = {
        timeData: this.hours,
        date: this.date,
        ID: this.Id,
      };
      axios.post(path, payload)
        .then(() => {
          this.importData();
        })
        .catch((error) => {
          // eslint-disable-next-line
            console.log(error);
          this.importData();
        });
      this.$emit('submit')
    },
  },
  mounted() {
    axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
    this.Id = localStorage.getItem('id');
  },
};
</script>
