<template>
  <div>
    <v-container align="right">
      <v-layout justify-center>
        <v-flex xs1 sm10 class="pa-1">
          <table class="table table-hover">
            <thead>
              <tr>
                <th>Name</th>
                <th>Date</th>
                <th>Amount</th>
                <th scope="col" width="50px">Paid</th>
                <th scope="col" width="50px">Print</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(hour, index) in hours" :key="index">
                <td>{{hour.Name}}</td>
                <td>{{hour.Date}}</td>
                <td>{{hour.Amount}}</td>
                <td>
                  <v-checkbox v-model="hour.Approved" color="blue" hide-details />
                </td>
                <td>
                  <v-btn @click='print(hour)'>Print</v-btn>
                </td>
              </tr>
            </tbody>
          </table>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import { constants } from "crypto";
import Axios from 'axios';

export default {
  components: {},
  props: {},
  data() {
    return {
      employee: {}
    };
  },

  computed: {
    hours() {
      return this.$store.getters.getJobHours;
    }
  },
  watch: {
    hours: {
      deep: true,
      handler() {
        this.$store.commit("editHours", this.hours);
      }
    }
  },

  methods: {
    print(hour) {
       const path = 'http://54.206.58.233:5000/pdf/job'
            const payload = {
                ID : this.$store.getters.getJob.Details.ID,
                Employee_Id: hour.Employee_Id,
                date: hour.Date
            }
            Axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
            Axios.post(path,payload)
              .then((res)=>{
                window.location.href = res.data
              })
    }
  }
};
</script>

