<script>
import axios from 'axios';
import { debuglog } from 'util';
import Employees from './Employees.vue';

export default {
  components: {
    Employees,
  },
  props: ['dateInput'],
  data() {
    return {
      date: '',
      tokenStr: '',
      menu: false,
      headers: [
        {
          text: 'Job',
          align: 'left',
          sortable: false,
          value: 'job',
        },
        { text: 'Monday', sortable: false, value: 'monday' },
        { text: 'Tuesday', sortable: false, value: 'tuesday' },
        { text: 'Wednesday', sortable: false, value: 'wednesday' },
        { text: 'Thursday', sortable: false, value: 'thursday' },
        { text: 'Friday', sortable: false, value: 'friday' },
        { text: 'Saturday', sortable: false, value: 'sunday' },
        { text: 'Sunday', sortable: false, value: 'sunday' },
      ],
      hours: [],
      Id: '',
      jobs: [],
      job: ''
    };
  },

  computed: {
    
    total () {
      var total = 0;
      for(var i = 0; i < this.hours.Hours.length;i = i + 1) {
        total = total + this.hours.Hours[i].monday.Hours + this.hours.Hours[i].tuesday.Hours + this.hours.Hours[i].wednesday.Hours + this.hours.Hours[i].thursday.Hours + this.hours.Hours[i].friday.Hours + this.hours.Hours[i].saturday.Hours + this.hours.Hours[i].sunday.Hours
      }

      return total
    }

  },

  methods: {
    updateDate(date, Id) {
      console.log(date);
      this.date = date;
      this.Id = Id;
      this.importData();
    },
    save(date) {
      this.$refs.menu.save(date);
      this.importData();
    },
    importData() {
      const path = 'http://54.206.58.233:5000/hours/get';
      const payload = {
        Date: this.date,
        ID: this.Id,
        filters: this.job
      };
      axios.post(path, payload)
        .then((res) => {
          this.hours = res.data;
        })
        .catch((error) => {
          // eslint-disable-next-line
            console.error(error);
        });
    },
    exportData() {
      const path = 'http://54.206.58.233:5000/hours/add';
      const date_send = {
        Date: this.date,
      };
      console.log(this.date);
      const payload = {
        timeData: this.hours,
        date: this.date,
        ID: this.Id,
      };
      axios.post(path, payload)
        .then(() => {
          this.importData();
          this.$store.dispatch('getAllJobs')
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.log(error);
          this.importData();
        });
    },
    getJobs () {
      const path = 'http://54.206.58.233:5000/job/combo'
      const payload = {
        ID: this.Id
      }
      axios.post(path,payload)
        .then((res)=>{
          this.jobs = res.data
        })
    }
  },
  mounted() {
    this.date = `${(new Date()).getFullYear()}-${(new Date()).getMonth() + 1}-${(new Date()).getDate()}`;
    axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
    this.Id = this.$store.getters.getAuthKey.Id
    this.importData();
    this.getJobs()
  },

};

</script>

