<template>
  <div>
    <v-container align="right">
      <v-layout justify-center>
        <v-flex xs1 sm10 class="pa-1">
          <v-combobox v-model="employee" :items="employeeList" label="Add Employee" @change='add'/>
          <table class="table table-hover">
            <thead>
              <tr>
                <th>Name</th>
                <th>Pay Rate</th>
                <th>Charge Rate</th>
                <th scope="col" width="50px" />
              </tr>
            </thead>
            <tbody>
              <tr v-for="(person, index) in people" :key="index">
                <td>
                  <v-text-field v-model="person.Name" label="Name" color="blue" />
                </td>
                <td>
                  <v-text-field v-model="person.PayRate" label="Pay Rate" color="blue" />
                </td>
                <td>
                  <v-text-field v-model="person.ChargeRate" label="Charge Rate" color="blue" />
                </td>
                <td class="box">
                  <v-checkbox v-model="person.select" color="blue" hide-details />
                </td>
              </tr>
            </tbody>
          </table>
          <v-btn outline color="red" @click="remove">Remove</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import { constants } from "crypto";

export default {
  components: {},
  props: {},
  data() {
    return {
        employee: ''
    };
  },

  computed: {
    employeeList() {
      return this.$store.getters.getEmployeeList;
    },
    people() {
      return this.$store.getters.getJobEmployees;
    }
  },
  watch: {
    people: {
      deep: true,
      handler() {
        this.$store.commit("editEmployees", this.people);
      }
    }
  },

  methods: {
      add() {
          var newEmployee = {
              ID: this.employee.value,
              Name: this.employee.text,
              ChargRate: '',
              PayRate: '',
          }
          this.people.push(newEmployee)
      },
      remove() {
        let i;
        const length = parseInt(this.people.length);
        for (i = length - 1; i >= 0; i--) {
          if (this.people[i].select == true) {
            this.people.splice(i, 1);
          }
        }
      }
  }
};
</script>

<style>
table {
  width: calc(100% + 30px);
  table-layout: fixed;
  border-spacing: 15px 0px;
  margin: 0px -15px;
}

.box {
  width: 30px;
}

span.b {
  width: 400px;
  display: inline-block;
  padding: 5px;
}
</style>
