<template>
  <div>
    <div>
      <v-tabs v-model="active" color="cyan" dark>
        <v-toolbar color="blue darken-4">
          <v-spacer />
          <v-tab>Summary</v-tab>

          <v-tab>Details</v-tab>
          <v-tab>People</v-tab>
          <v-tab>Expenses</v-tab>
          <v-tab>Invoices</v-tab>
          <v-tab>Hours</v-tab>
        </v-toolbar>

        <v-tab-item>
            <Summary/>
        </v-tab-item>
        <v-tab-item>
            <Details/>
        </v-tab-item>
        <v-tab-item>
            <People/>
        </v-tab-item>
        <v-tab-item>
            <Expense/>
        </v-tab-item>
        <v-tab-item>
            <Invoice/>
        </v-tab-item>
        <v-tab-item>
            <HoursJob/>
        </v-tab-item>
      </v-tabs>

      <v-container align="center">
        <v-layout justify-center align-start>
          <v-flex s1 sm3 class="pa-1">
            <div>
              <v-btn @click="save">Submit</v-btn>
            </div>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Details from './Details.vue'
import People from './People.vue'
import Invoice from './Invoice.vue'
import Expense from './Expense.vue'
import HoursJob from './HoursJob.vue'
import Summary from './Summary.vue'

export default {
  components: {
    Details,
    People,
    Invoice,
    Expense,
    HoursJob,
    Summary
  },
  data() {
    return {
      active: null
    };
  },
  computed: {
    job () {
      return this.$store.getters.getJob;
    }
  },

  methods: {
    setTab(tab) {
      this.active = tab
    },
      save() {
        if((this.job.Job.PONo != '' || this.job.Job.Project_Number != '' ) && this.job.Job.Lead != ''){ 
          this.$store.dispatch('addJob')
          this.$emit('submit')
        }
      }
  }
}
   
</script>

