<template>
  <div>
    <v-container align="right">
      <v-layout justify-center>
        <v-flex xs1 sm10 class="pa-1">
          <v-text-field v-model="job.Name" label="Project Name" color="blue" />
          <v-combobox v-model="job.Client" :items="client" label="Client" />
          <v-text-field type='number' v-model="job.Budget_Hours" label="Budget(Hrs)" color="blue" />
          <v-text-field prefix='$' type='number'
            v-model="job.Est_Project_Cost"
            label="Estimate of Project Cost"
            color="blue"
          />
          <v-text-field prefix='$' type='number' v-model="job.Project_Value" label="Project Value" color="blue" />
          <v-text-field v-model="job.PONo" label="PO#" color="blue" />
          <v-text-field v-model="job.Project_Number" label="Project Number" color="blue" />
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="job.Complete_Date"
                label="Completion Date"
                prepend-icon="event"
                readonly
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker first-day-of-week="1" ref="picker" dark v-model="job.Complete_Date" @change="save"></v-date-picker>
          </v-menu>
          <v-combobox v-model="job.Lead" :items="employeeList" label="Project Lead" />
          <v-combobox v-model="job.Status" :items="status" label="Status" />
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import { constants } from "crypto";

export default {
  components: {},
  props: {},
  data() {
    return {
      menu: false,
      status: [
        { text: "Planning", value: 0 },
        { text: "Active", value: 1 },
        { text: "Complete", value: 2 },
		    { text: 'Deleted', value: 3}
      ]
    };
  },

  computed: {
    employeeList() {
      return this.$store.getters.getEmployeeList;
    },
    job() {
      return this.$store.getters.getJobDetails;
    },
    client () {
      let jobs = this.$store.getters.getJobs;
      var clients = []
      for(var i = 0; i < jobs.length; i++) {
        clients.push(jobs[i].Job.Client)
      }
      return clients

    }
  },
  watch: {
    job: {
      deep: true,
      handler() {
        this.$store.commit("editDetails", this.job);
      }
    }
  },

  methods: {
    save(date) {
      this.$refs.menu.save(date);
      this.importData();
    },
  }
};
</script>

