<template>
    <div>
    <v-app id="inspire">
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Register</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                   <v-combobox v-model="employee" :items="employees" label="Person" />
                   <v-text-field v-model="username" id="username" prepend-icon="person" name="username" label="Username" type="text"></v-text-field>
                  <v-text-field v-model="password" id="password" prepend-icon="lock" name="password" label="Password" type="password"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="register">Register</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
  </v-app>
    </div>
</template>

<script>
import Axios from 'axios';
export default {

    data() {
        return {
            password: '',
            employee: '',
            username: ''
        }
    },

    computed : {
        employees () {
            return this.$store.getters.getEmployeeList
        },
    },
    methods : {
        register () {
            const path = 'http://54.206.58.233:5000/register'
            const payload = {
                ID : this.employee.value,
                Password: this.password,
                Username: this.username
            }
            Axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
            Axios.post(path,payload)
			this.$router.push('/Employees')
        }
    }


}
</script>

