<template>
  <div>
    <v-container align="right">
      <v-layout justify-center>
        <v-flex xs1 sm10 class="pa-1">
          <table class="table table-hover">
            <thead>
              <tr>
                <th>Item</th>
                <th>Comment</th>
                <th>Category</th>
                <th>Cost</th>
                <th>Provider</th>
                <th scope="col" width="50px" />
                <th>Document</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(expense, index) in expenses" :key="index">
                <td>
                  <v-text-field v-model="expense.Name" label="Item" color="blue" />
                </td>
                <td>
                  <v-text-field v-model="expense.Comment" label="Comment" color="blue" />
                </td>
                <td>
                  <v-text-field v-model="expense.Category" label="Category" color="blue" />
                </td>
                <td>
                  <v-text-field prefix='$' type='number' v-model="expense.Cost" label="Amount" color="blue"></v-text-field>
                </td>
                <td>
                  <v-text-field v-model="expense.Provider" label="Provider" color="blue" />
                </td>
                <td>
                  <v-checkbox v-model="expense.select" color="blue" hide-details />
                </td>
                <td>
                  <a
                    v-bind:href="'http://54.206.58.233:5000/download/' + expense.AttachFile"
                  >{{expense.AttachFile}}</a>

                  <input
                    v-if="!expense.AttachFile"
                    type="file"
                    @change="onFilePicked($event, index)"
                  />
                </td>
              </tr>
            </tbody>
          </table>
          <v-btn @click="add">Add</v-btn>
          <v-btn outline color="red" @click="remove">Remove</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import { constants } from "crypto";

export default {
  components: {},
  props: {},
  data() {
    return {
      filePath: "/storage/",
      fileName: [],
      file: null
    };
  },

  computed: {
    expenses() {
      return this.$store.getters.getJobExpenses;
    }
  },
  watch: {
    expenses: {
      deep: true,
      handler() {
        this.$store.commit("editExpenses", this.expenses);
      }
    }
  },

  methods: {
    onFilePicked(e, index) {
      let str = e.target.value;
      this.fileName[index] = str.substring(str.lastIndexOf("\\") + 1);
      this.expenses[index].AttachFile = this.fileName[index];

      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      // this.createFile(files[0]);

      var data = new FormData();
      data.append("foo", "bar");
      data.append("file", e.target.files[0]);

      axios
        .post("http://54.206.58.233:5000/upload/evidence", data, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(function(response) {
          console.log(response);
        });
    },

    getPath(index) {
      return this.filePath + this.fileName[index];
    },
    setPath(filePath, index) {
      this.fileName[index] = filePath;
    },
    add() {
      var expense = {
        Name: "",
        Date: "",
        Amount: ""
      };
      this.expenses.push(expense);
    },
    remove() {
      let i;
      const length = parseInt(this.expenses.length);
      for (i = length - 1; i >= 0; i--) {
        if (this.expenses[i].select == true) {
          this.expenses.splice(i, 1);
        }
      }
    }
  },

  created() {
    axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
  }

};
</script>

