<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Login form</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field v-model="username" prepend-icon="person" name="login" label="Login" type="text"></v-text-field>
                  <v-text-field v-model="password" id="password" prepend-icon="lock" name="password" label="Password" type="password"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="login">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import Axios from 'axios';

export default {
  data: () => ({
    drawer: null,
    username: '',
    password: '',
  }),
  props: {
    source: String,
  },
  methods: {
    login() {
      const path = 'http://54.206.58.233:5000/login';
      const payload = {
        username: this.username,
        password: this.password,
      };
      Axios.post(path, payload)
        .then((res) => {
          console.log(res);
          this.$store.commit('updateAuthKey', res.data.data)
          this.$store.dispatch('getAllJobs')
          this.$store.dispatch('getEmployeeList')
          this.$router.push('/timeSheet');
        })
       .catch(error => {
          // eslint-disable-next-line
          console.error(error);
        });      
    },
  },
};
</script>
