import Vue from 'vue';
import Vuex from 'vuex';
import Axios from 'axios';
import router from './router.js'
import { stat } from 'fs';

Vue.use(Vuex);

export default new Vuex.Store({

  state: {
    jobs : [],
    message: '',
    displayeMessage: false,
    EmployeeList : [],
    job: {
      Job:{
        ID:'',
        Name: '',
        Client: '',
        Est_Project_Cost: '',
        Lead: '',
        PONo:'',
        Project_Value:''
    },
      Expenses:{},
      Invoices:{},
      Employees: {},
      Hours:{}
    },
    jobList :[],
    authInfo : {
      token: '',
      Id: '',
      role: 'loggedout',
    }
  },
  
  mutations: {

    changeJobs(state, payload) {
      state.jobs = payload
    },
    changeMessage(state){
      state.message = payload
      state.displayeMessage = true
    },
    changeEmployeeList(state,payload) {
      state.EmployeeList = payload
    },
    changeJob(state,payload) {

      for(var i = 0; i < state.jobs.length; i++) {
        console.log(state.jobs[i].Job.ID)
        if(state.jobs[i].Job.ID === payload){
          
          state.job = state.jobs[i] 
        }
      }
    },
    updateAuthKey(state, payload) {
      state.authInfo.Id = payload.id
      state.authInfo.role = payload.role
      state.authInfo.token = payload.token
      localStorage.setItem('token', payload.token);
      localStorage.setItem('Id', payload.id)
      Axios.defaults.headers.common.Authorization = `Bearer ${payload.token}`;

    },


    addJob(state){
      state.job = {
        Job:{
          ID:'',
          Name: '',
          Client: '',
          Est_Project_Cost: '',
          Lead: '',
          PONo:'',
          Project_Value:''
      },
        Expenses:[],
        Invoices:[],
        Employees: [],
        Hours:[],
        Summary: {
          
        }
      }
    },

    //Job edit mutators

    editDetails(state,payload) {
      state.job.Details = payload
    },
    editExpenses(state,payload) {
      state.job.Expenses = payload
    },
    editEmployees(state,payload) {
      state.job.Employees = payload
    },
    editInvoices(state,payload) {
      state.job.Invoices = payload
    },
    editHours(state,payload) {
      state.job.Hours = payload
    },

    changeJobList(state, payload) {
      state.jobList = payload
    }


  },

  getters : {

    getJobs(state) {
      return state.jobs
    },

    getEmployeeList(state) {
      return state.EmployeeList
    },
    getJobSummary(state) {
      return state.job.Summary
    },
    getJobDetails(state){
      return state.job.Job
    },
    getJobEmployees(state){
      return state.job.Employees
    },
    getJobExpenses(state){
      return state.job.Expenses
    },
    getJobExpenses(state){
      return state.job.Expenses
    },
    getJobInvoices(state){
      return state.job.Invoices
    },
    getJobHours(state){
      return state.job.Hours
    },
    getJob(state) {
      return state.job
    },
    getAuthKey(state){
      return state.authInfo
    }


  },

  actions: {

    getAllJobs({commit, dispatch}) {
      const path = 'http://54.206.58.233:5000/job/get'
      Axios.get(path) 
        .then((res) => {
          commit('changeJobs', res.data)
          dispatch('getJobList')
        })
    },

    getEmployeeList({commit}){
      const path = 'http://54.206.58.233:5000/employee/listEmployee'
      Axios.get(path)
        .then((res)=>{
          commit('changeEmployeeList', res.data)
        })

    },

    addJob({commit, state, dispatch}) {
      const path = 'http://54.206.58.233:5000/job/add'
      const payload = state.job
      Axios.post(path,payload)
        .then((res)=>{
          dispatch('getAllJobs')
        })

    },
    getJobList({commit}) {
      const path ='http://54.206.58.233:5000/job/list'
      Axios.get(path)
      .then((res)=>{
          commit('changeJobList',res.data)
      })
  },

  renew({commit, dispatch}) {
    const path = 'http://54.206.58.233:5000/renew'
    const payload = {
      ID: localStorage.getItem('Id')
    }
    Axios.defaults.headers.common.Authorization = `Bearer ${localStorage.getItem('token')}`;
    Axios.post(path,payload)
      .then((res)=>{
        commit('updateAuthKey', res.data.data)
        dispatch('getAllJobs')
        dispatch('getEmployeeList')
		router.push('/timeSheet')
    })
    .catch((error) => {
      router.push('/login')
    })
  }


  },
});
