import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import ComplexHeader from './components/TimeSheet.vue'
import Login from './components/LoginBox.vue'
import Employees from './components/Employees.vue'
import JobView from './views/JobView.vue'
import Register from './components/Register.vue'

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login,
    },
    {
      path:'/Jobs',
      name: 'Jobs',
      component: JobView
    },
    {
      path: '/Employees',
      name: 'Employees',
      component: Employees,
    },
    {
      path: '/TimeSheet',
      name: 'Time Sheet',
      component: ComplexHeader,
    },
    { 
      path: '/Login',
      name: 'Login',
      component: Login,
    },
    { 
      path: '/Register',
      name: 'Register',
      component: Register,
    },
  ],
});
