<template>
  <div>
        <hr />
        <br />
        <br />
        <v-btn type="button" class="btn btn-success btn-sm" @click='addNewEmployee()'>Add Person</v-btn>
        <br />
        <br />
        <table class="table table-hover">
          <thead>
            <tr>
              <th scope="col">Name</th>
              <th scope="col">Email</th>
              <th scope="col">Role</th>
              <th scope="col">Company</th>
              <th scope="col">PayRate</th>
              <th scope="col">ChargeRate</th>
              <th scope="col">Project Assigned</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(employee, index) in Employee" :key="index">
              <td @click='editEmployee(employee)'>{{ employee.Username }}</td>
              <td @click='editEmployee(employee)'>{{ employee.Email }}</td>
              <td @click='editEmployee(employee)'>{{ employee.Role }}</td>
              <td @click='editEmployee(employee)'>{{ employee.Company }}</td>
              <td @click='editEmployee(employee)'>{{ employee.PayRate }}</td>
              <td @click='editEmployee(employee)'>{{ employee.ChargeRate }}</td>
              <!-- <td>
                <v-btn
                  type="button"
                  class="btn btn-warning btn-sm"
                  v-b-modal.activity-modal
                  @click="getActivity(employee)"
                >View Activity</v-btn>
              </td> -->
              <td @click='editEmployee(employee)'>{{AssignedProject[index]}}</td>
            </tr>
          </tbody>
        </table>
    <b-modal ref="addEmployeeModal" id="employee-modal" title="Add a new Employee" hide-footer>
      <b-form @submit="onSubmit" @reset="onReset" class="w-100">
        <b-form-group
          id="form-id-group"
          label="id:"
          label-for="form-id-input"
          align="left"
          style="display:none;"
        >
          <b-form-input
            id="form-id-input"
            type="text"
            v-model="addEmployeeForm.ID"
          ></b-form-input>
        </b-form-group>

        
        <b-form-group
          id="form-Username-group"
          label="Name:"
          label-for="form-Username-input"
          align="left"
        >
          <b-form-input
            id="form-Username-input"
            type="text"
            v-model="addEmployeeForm.Username"
            required
            placeholder="Enter Name"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="form-Email-group"
          label="Email:"
          label-for="form-Email-input"
          align="left"
        >
          <b-form-input
            id="form-Email-input"
            type="text"
            v-model="addEmployeeForm.Email"
            required
            placeholder="Enter Email"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="form-Role-group" label="Role:" label-for="form-Role-input" align="left">
          <v-combobox 
          id="form-Role-input"
          v-model="addEmployeeForm.Role" 
          :items="roleList"/>
        </b-form-group>
        <b-form-group id="form-Company-group" label="Company:" label-for="form-Company-input" align="left">
          <b-form-input
            id="form-Company-input"
            type="text"
            v-model="addEmployeeForm.Company"
            required
            placeholder="Enter Company"
          ></b-form-input>
        </b-form-group>
          <b-form-group v-if="addEmployeeForm.Role!='Client'" id="form-Pay-group" label="Pay Rate:" label-for="form-Charge-input" align="left">
          <b-form-input
            id="form-Charge-input"
            type="number"
            v-model="addEmployeeForm.ChargeRate"
            required
            placeholder="Enter Pay Rate"
          ></b-form-input>
          </b-form-group>
          <b-form-group v-if="addEmployeeForm.Role!='Client'" id="form-Pay-group" label="Charge Rate:" label-for="form-Pay-input" align="left">
          <b-form-input
            id="form-Pay-input"
            type="number"
            v-model="addEmployeeForm.PayRate"
            required
            placeholder="Enter Charge Rate"
          ></b-form-input>
        </b-form-group>
        <v-btn type="submit" variant="primary">Submit</v-btn>
        <v-btn type="reset" variant="danger">Reset</v-btn>
      </b-form>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      roleList: [
        'Employee',
        'Admin',
        'Client'        
      ],
      items: [],
      tokenStr: "",
      hours: [],
      Employee: [],
      AssignedProject: [],
      addEmployeeForm: {
        ID: "",
        Username: "",
        Email: "",
        Role: "",
        Company: "",
        ChargeRate: 0,
        PayRate: 0
      }
    };
  },
  methods: {
    getActivity(employee) {
      const path = "http://54.206.58.233:5000/activity/individual";
      const payload = {
        ID: employee.ID
      };
      axios
        .post(path, payload)
        .then(res => {
          this.items = res.data;
          
        })
        .catch(error => {
          // eslint-disable-next-line
          console.error(error);
        });
    },
    getEmployees() {
      const path = "http://54.206.58.233:5000/employee/list";
      axios
        .get(path)
        .then((res) => {
          console.log(res.data)
          this.Employee = res.data;
        })
        .catch(error => {
          // eslint-disable-next-line
          console.error(error);
        });
    },
    addEmployee(payload) {
      const path = "http://54.206.58.233:5000/employee/add";
      axios
        .post(path, payload)
        .then(() => {
          this.getEmployees();
		  this.$store.dispatch('getEmployeeList')
		  this.$store.dispatch('getAllJobs')
        })
        .catch(error => {
          // eslint-disable-next-line
          console.log(error);
          this.getEmployees();
        });
    },
    onSubmit(evt) {
      evt.preventDefault();
      this.$refs.addEmployeeModal.hide();
      const payload = {
        ID: this.addEmployeeForm.ID,
        Username: this.addEmployeeForm.Username,
        Email: this.addEmployeeForm.Email,
        Role: this.addEmployeeForm.Role, // property shorthand
        Company: this.addEmployeeForm.Company,
        PayRate: this.addEmployeeForm.ChargeRate,
        ChargeRate: this.addEmployeeForm.PayRate
      };
      
      this.addEmployee(payload);
      // this.initForm();
    },
    onReset(evt) {
      evt.preventDefault();
      this.$refs.addEmployeeModal.hide();
      // this.initForm();
    },
    removeEmployee(employeeID) {
      const path = "http://127.0.0.1:5000/removeEmployee";
      const payload = {
        ID: employeeID
      };
      axios
        .post(path, payload)
        .then(() => {
          this.getEmployees();
        })
        .catch(error => {
          // eslint-disable-next-line
          console.error(error);
          this.getEmployees();
        });
    },
    editEmployee(employee) {
      this.addEmployeeForm = employee
      this.$refs.addEmployeeModal.show()
    },
    addNewEmployee() {
      this.addEmployeeForm = {
		ChargeRate : 0,
		PayRate : 0
	};
      this.$refs.addEmployeeModal.show()
    }
  },
  created() {
    this.tokenStr = localStorage.getItem("token");
    axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
    this.getEmployees();
  }
};
</script>
<style>
.table th, .table td{
  vertical-align: middle !important;
}

#form-Role-input{
  display: block;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
}

</style>
